.taskboard{
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: #fff;
    
    font-family: 'Montserrat';
}
.taskboard_heading{
    display: flex;
    padding: 10px;
    font-size: 18px;
    font-weight: 700;
    color: black;
    background-color: #fff;
}
.taskboard_body{
    display: flex;
    background-color: #eee;
}
.task_container{
    background-color: white;
    margin: 10px;
    display: flex;
    flex-direction: column;
    flex: 0.33;
}
.task_container_heading{
           display: flex;
           font-size: 18px;
           font-weight: 600;
           flex-direction: column;
           padding: 20px;
}
.task_container_heading span{
    color: #eee;
    padding: 10px 0;
    font-size: 15px;
}