.howitwork{
    display: flex;
    flex-direction: column;
    padding: 30px;
    background-color: #f7f7f7;
}
.howitwork-top{
    display: flex;
    justify-content: center;
   
    flex-direction: column;
}
.hiw-topheading{
    display: flex; 
    justify-content: center;
    font-size: 30px;
    padding: 30px 0 20px 0 ;
    font-weight: 700;
}
.hiw-toptag{
    display: flex;
    justify-content: center;
    color: gray;
}
.howitwork-blog{
    display: flex;
    padding: 30px 100px ;
}
.hiw-blog{
 background-color: white;
 border-radius: 10px;
 margin: 20px 20px;
 min-width: 300px;
 transition: all ease 0.4s;
}
.hiw-blog:hover{
    -webkit-box-shadow: -3px 6px 11px -3px rgba(184,180,184,0.84);
-moz-box-shadow: -3px 6px 11px -3px rgba(184,180,184,0.84);
box-shadow: -3px 6px 11px -3px rgba(184,180,184,0.84);
}
.hiw-blogtop{
    display: flex;
    justify-content: center;
}
.hiw-blogtop img{
    width: 100%;
    height: 220px;
    border-radius: 20px;
    padding: 10px;
}
.hiw-blogbody{
    padding: 10px;
}
.hiw-blogbody .ques{
    color: red;
    padding:  0;
}









.hiw-blogbody .ans{
    color: grey;
    font-weight: 400;
    padding: 10px 0;
}
@media only screen and (max-width:700px){
    .howitwork{
        display: flex;
        flex-direction: column;
        padding: 30px;
        margin-top: 80px;
    }
    .howitwork-blog{
        display: block;
        padding: 30px 10px ;
    }
    .hiw-blog{
        background-color: white;
        border-radius: 10px;
        margin: 20px 0px;
        min-width: 250px;
        transition: all ease 0.4s;
       }
}



